@import "_variable";
@import "_modules/reset";
@import "_mixin/clearfix";
@import "_partials/grid";
@import "_mixin/imgquery";
@import "_partials/px-to-rem";
@import "_partials/typography";
@import "_partials/preloader";
@import "_partials/hamburgers/hamburgers.scss";
@import "_partials/globle";
@import "_partials/color";
@import "_mixin/animate/animate";
@import "_partials/slick";
@import "_partials/slick-theme";
@import "_partials/animate";
@import "_partials/header";
@import "_partials/navbar";
@import "_partials/button";
html{
    
}
//手機版背景
@mixin background-small{
    background-image: url("../images/bg-small.svg");
    background-repeat: no-repeat;
    background-color:$primary-color;
    background-size:contain;
}

body{
    overflow-x: hidden;
    background-color: $primary-color;
    .top_fill_color{
        position:absolute;
        width:100vw;
        height:rem(300);
        background-color: #FFE983;
    }
}

#index,#step{
    position: relative;
    @include background-small;
    @media #{$medium-and-down}{
        background-position: center -50%;
    }
    @media #{$small-and-down}{
        background-image: url("../images/bg-medium.svg");
        background-position: center top;
    }
    //背景圖調整
    @media #{$small-and-up}{
        background-image: url("../images/bg-medium.svg");
    }
    @media #{$medium-and-up}{
        background-image: url("../images/bg-medium.svg");
    }
}

.carousel{
    .body{
        text-align: center;
    }
}

.download_link{
    display: block;
    color:black;
    text-decoration: underline;
    padding:.5rem 0;
}



.title{
    max-width:rem(1036);
    height:rem(251);
    margin:1rem auto 0 auto;
    background: url("../images/title.png") no-repeat center top;
    background-size:contain;
}

.ribbon-on-btn{
    background-image: url("../images/ribbon_on_button.svg");
    background-repeat: no-repeat;
    background-position: left top;
    background-size: 70%;
}
//選單 回首頁
nav,#mobile-nav{
    ul{
        li{
            position: relative;
            &:first-child{
                a{
                    position: relative;
                    color: #fff;
                    background-image: url("../images/school.svg");
                    background-repeat: no-repeat;
                    background-position: 30% center;
                    background-size: 47px 34px;
                    background-color: $purple-color;
                    padding-left: 6rem;
                }
                &::after{
                    content:"";
                     position: absolute;
                     left:0;
                     top:10px;
                     width:80px;
                     height:50px;
                     background-image: url("../images/ribbon_on_button.svg");
                     background-repeat: no-repeat;
                     background-position: left top;
                     background-size: cover;
                 }
            }

        }
    }
}

//首頁公告
@mixin poster_area{
    display:flex;
    flex-direction: column;
    text-align:center;
    margin-bottom:8rem;
    margin-top:19rem;

    .flaps{
        flex-basis: rem(30);
    }
}

.poster_area{
    .step1,.step2{
        flex:initial
    }
    @include poster_area;

}

.block{
    position: relative;
    border-radius: 1rem;
    background-color:#E8E8E8;
	border: solid #E8E8E8 10px; 
    box-shadow: inset 0px 0px 0px 4px #E8E8E8;
    background-color:#fff;
    .block_inner{
        width:100%;
        height:100%;
        padding:rem(18);
        border-radius: 1rem;
	    border: solid $primary-color 3px; 
    }  
}

.ribbon{
    width:100%;
    height:rem(70);
    line-height: rem(50);
    background-repeat: no-repeat;
    background-position: center top;
    background-size:contain;
    @media #{$medium-and-down}{
        height:6.3rem;
        line-height: 5.3rem;
    }
}

.ribbon_1{
    background-image: url("../images/ribbon_1.png");
}

.ribbon_2{
    background-image: url("../images/ribbon_2.png");
}

.mark_bg{
    display: block;
    margin-top:1rem;
    padding:.5rem;
}

.policy{
    font-size:rem(12);
    line-height: 140%;
    height:rem(240);
    margin-top:2rem;
    overflow-y: scroll;
    background-color:rgba(256,256,256,.3);
    .area{
        padding:1rem;
        li{
            padding:.2rem;
        }
    }
}

//迎新布條
.welcome_cloth{
    height: 20rem;
    position:absolute;
    z-index:2;
    top:10.5rem;
    left: 0;
    right: 0;
    background: url("../images/welcome.svg") no-repeat center top;
    background-size: cover;
    @media #{$medium-and-down} {
        top:13.5rem;
        background-size: 128%;
    }
    @media #{$small-and-down} {
        top:13.5rem;
        background-size: 128%;
    }
    @media #{$large-and-up} {
        width: rem(982);
        top:19.2rem;
        left:50%;
        transform: translateX(-50%);
        background-size: 80%;       
    }
}


//方向牌
.direction{
    position: absolute;
    z-index: 3;
    width: rem(332);
    height:rem(215);
    top:rem(760);
    right:rem(360);
    .glass_doll{
        position: absolute;
        left: -47px;
        bottom: -3px;
        width: rem(152);
        background:url("../images/glass_doll_shadow.svg") no-repeat center bottom;
      }
    .pointer{
        cursor: pointer;
    }
}

@media #{$medium-and-down} {
    .direction{
        top: 18rem;
        right: -2rem;
        transform: scale(.8);
    }
}
@media #{$small-and-down} {
    .direction{
        top: 22rem;
        right: -2rem;
        transform: scale(.8);
    }
}

//雪片
.snow_area{
    width:100%;
    overflow:hidden
}
#snow_piece{
    width:300px;
    height:200px;
    position:absolute;
}
.snow{
    width:8px;
    height:8px;
    position:absolute;
    background-color: $secondary-color;
    background-size: 100% 100%;
}

//行走路線
.walking-area{
    position: absolute;
    @media #{$large-and-down}{           
        left:0;
        right:0;
        height:40rem;
        z-index:0;
        transform:translateY(-20%);    
    }
}

//行走小人
.walking_doll{
    position: absolute;
    bottom: -5%;
	z-index: 1;
}

.doll{
    width:rem(192);
}

.anim_path{
    display:none
}

.forIEwlalking{
    display: none;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .walking-area#walkArea{
        display:block
    }
    @media #{$large-and-up}{
        .forIEwlalking{
            display:block;
            position:absolute;
            top:40%;
            left:50%
        }
        
        .walking-area#walkArea{
            display:none
        }
   }
}



#index{
    @include background-small;

    @media #{$small-and-up}{
        background-image: url("../images/bg-medium.svg");
        background-position: center 50%;
        background-size: contain;
        h3{
            font-size: 2.6rem;
        }
    }
    @media #{$medium-and-up}{
        background-image: url("../images/bg-medium.svg");
        background-position: center -20%;
        background-size: 1080px;
    }
    @media #{$medium-and-up}{
        .poster_area{
            margin-top: 32rem;
        }
        .walking-area{
            height: 58rem;
        }
        .direction{
            top: 33.3rem;
            right: 0;
        }
        .doll{
            width:16rem;
        }
    }


    @include background-small;

    @media #{$large-and-up}{
        .walking-area{
            width: 480px;
            height: 100px;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
        }

        background: url("../images/bg.svg") no-repeat center 20% $primary-color;
        background-size:cover;
        .title{
            margin:0 auto 0 auto;
        }
        .anim_path{
            display:block;
            position:absolute;
            left:0;
            bottom:0;
            width:300px;
            height:100px;
        }
        .poster_area{
            flex-direction: row;    
            margin-top:-2rem;
            .step1,.step2{
                flex:1
            }
            .flaps{
                flex:1.1
            }
        }

        .direction{
            top: 47.5rem;
            right: 22.5rem;
        }
   }
}

#step{
    .container{
        position: relative;
        .wrapper{
            position: absolute;
            height:100vh;
        }
    } 
    
    .button-area{
        .button{
            width:initial;
            margin-right: 2rem;
        }
        @media #{$medium-and-down} {
            .button{
                width:100%;
                margin-right: 0;
            }   
        }
    }
    
    h3{
        font-size: 2rem;
    }
    .circle_icon{
        text-align: center;
        width:rem(28);
        line-height:rem(28);
        display: inline-block;
        margin-right:1rem;
        background-image: url("../images/ellipse.png");
        background-repeat: no-repeat;
        background-position: left top;
        background-size: contain;
    }
    .poster_area{
        margin-bottom: 0;
    }
    .ribbon{
        height:6.3rem;
        line-height: 3.3rem;
    }

    .content{
        text-align: left;
        line-height: 2.5rem;
        .inner{
            margin: initial;
            padding:initial;
        }
        ol,ul{
            margin: initial;
            padding:initial;
        }
        ul{
            li{
                padding-top: 1rem;
            }
        }
    }
    .app{
        flex-direction: column;
        .step_img{
            position: relative;
            flex: 1;
            margin:1rem;
        }
        .img_swith{
            background:url("../images/app_3.png") no-repeat center top #fff;
        }
    }
    .elementToFadeInAndOut {
        opacity: 0;
        animation: fade 2s linear;
    }
    
    
    @keyframes fade {
      0%,100% { opacity: 0 }
      50% { opacity: 1 }
    }

    @include background-small;

    @media #{$small-and-up}{
        background-image: url("../images/bg-medium.svg");
        background-position: center 30%;
        background-size: contain;
        h3{
            font-size: 2.6rem;
        }
    }
    @media #{$medium-and-up}{
        background-image: url("../images/bg-medium.svg");
        background-position: center -20%;
        background-size: 1080px;
    }
    //第二重頁面
    .step2{
        .content{
            display: flex;
            flex-direction: column;
            align-items: center;
           
        }
        .prize-img{
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .winnder_list{
            width: 272px;
            height: 200px;
        }
    }
    @media only screen and (min-width: 769px){
        .poster_area{
            margin-top: 31rem;
        }
        .direction {
            top: 34rem;
            right: 0;
        }
        .walking-area{
            height:56rem;
        }
        .doll{
            width:16rem;
        }

    }
    @media only screen and (min-width: 1025px){
        background: url("../images/bg-medium-contain.svg") no-repeat center top;
        background-size:1960px;
        .hide-show-doll{
            position:absolute;
            top:50%;
            right: -12%;
            width:164px;
            height:216px;
            overflow:hidden;
            background: url("../images/hide_doll.svg") no-repeat center top;
        }

        .walking-area{
            width:1280px;
            height:46rem;
            left:50%;
            transform: translateX(-50%);
        }
        .doll{
            width:16rem;
        }
        .title{
            margin:0 auto;
        }
        .welcome_cloth {
            width: 70%;
        }
        .poster_area{
            margin-top: 31rem;
        }
        .direction {
            position: absolute;
            top: 32.5rem;
            right: 0;
        }
        .content{
            padding:2rem;
        }
        .app{
            flex-direction: row;
        }
        .ribbon{
            line-height: 5.3rem;
        }

        .step2{
            .content{
                display:flex;
                flex-direction: row;
            }
            .body{
                flex:1;
                padding-right:2rem;
            }
            .prize-img{
                flex:1;
            }
        }
    }
}



.pagetop {
    position: fixed;
    width: rem(76);
    height: rem(109);
    display: block;
    right: 30px;
    bottom: 30px;
    z-index: 4;
    background: url('../images/rolltop.svg') no-repeat center;
    animation: slightMoving 2s both linear infinite;
    cursor: pointer;
}

  
@keyframes slightMoving {
0%, 100% {
    transform: translateY(0);
}
50% {
    transform: translateY(-10px);
}
}


.mark {
    color: $red-color;
    font-weight: 500;
}


.button {
    font-size:$button-fontsize;
    font-weight: 400;
    color: #fff;
    display: inline-block;
    border-radius: 10px;
    letter-spacing: 3px;
    border: 2px solid transparent;
    padding: 10px 30px;
    cursor: pointer;
    transition: all .3s;
    &:hover{
        background-color: $primary-color-light;
    }
}



//footer
footer {
    text-align: center;
    color: $lighen-ancent;
    margin-top:2rem;
    background-color: $footer-copyright-bg-color;

    @media #{$medium-and-up} {
    }

    .logo {
        width: rem(300);
        height: 7rem;
        padding: 2rem;
        margin: 2rem auto;
        display: block;
        background-image: url('../images/footer_large_logo.png');
        background-position: center top;

        @media #{$medium-and-up} {
            background-position: center 22px;
        }

        background-repeat:no-repeat;
    }

    .social-media {
        padding: 2rem 0;

        a {
            color: #666;
            font-size: 20px;
            padding-right: 1rem;

            &:hover {
                color: $primary-color
            }
        }
    }

    .copyright {
        color:#fff;
        font-size: rem(15);
        line-height: rem(80);
    }
}
